<template>
  <div>
    <v-layout row wrap>
        <v-flex>
            
    <vue-dropzone
    v-show="passwort.length != 0"
    transition="slide-y-transition"
      ref="myVueDropzone"
      :include-styling="false"
      :useCustomSlot="true"
      id="dropzone"
      @vdropzone-upload-progress="uploadProgress"
      :options="dropzoneOptions"
      @vdropzone-file-added="fileAdded"
      @vdropzone-sending-multiple="sendingFiles"
      @vdropzone-success-multiple="success"
    >

            
      <v-layout class="dropzone-container subheading" align-center pa-4>
        <v-flex class="file-selector text-xs-center" align-center >
          <figure>
            <v-icon x-large>cloud_upload</v-icon>
          </figure>
          <p>Ziehe deine Fotos und Videos hier hin</p>
          <p>
            <span>oder</span>
          </p>
          <button type="button">Durchsuche deinen PC</button>
        </v-flex>
      </v-layout>
    </vue-dropzone>
        </v-flex>

    </v-layout>



    <v-layout row align-center>
      <v-flex xs11>
        <v-progress-linear v-model="progress" height="10"></v-progress-linear>
      </v-flex>
      
      <v-flex xs1 class="text-xs-center">
        <span>{{progress}}%</span>
      </v-flex>
    </v-layout>

    <v-layout row wrap>
        <v-flex xs12 class="title">
            Tippe zur Sicherheit das     Passwort ein
        </v-flex>
        <v-flex xs12>
            <v-text-field
                name="pw"
                label="Sicherheitspasswort"
                v-model="passwort"
                single-line
                color="info"
                type="password"
              ></v-text-field>
        </v-flex>
    </v-layout>
  </div>
</template>

<script>
import vue2Dropzone from "vue2-dropzone";
import "vue2-dropzone/dist/vue2Dropzone.min.css";
// import AttachmentList from "./AttachmentList";
export default {
  name: "Attachment",
  data() {
    return {
      progress: 0,
      passwort: "",
      msg: "Welcome to Your Vue.js App",
      tempAttachments: [],
      attachments: [],
      dropzoneOptions: {
        // The Url Where Dropped or Selected files will be sent
        url: `php/upload.php`,
        method: "POST",
        // File Size allowed in MB
        maxFilesize: 102400000,
        // Authentication Headers like Access_Token of your application
        headers: {
          Authorization: `Access Token`
        },
        // The way you want to receive the files in the server
        paramName: function(n) {
          return "fileupload[]";
        },
        dictDefaultMessage: "Upload Files Here xD",
        includeStyling: false,
        previewsContainer: false,
        thumbnailWidth: 250,
        thumbnailHeight: 140,
        uploadMultiple: true,
        parallelUploads: 20
      }
    };
  },
  components: {
    vueDropzone: vue2Dropzone
    // AttachmentList: AttachmentList
  },
  methods: {
    // function called for every file dropped or selected
    fileAdded(file) {
      console.log("File Dropped => ", file);
      // Construct your file object to render in the UI
      let attachment = {};
      attachment._id = file.upload.uuid;
      attachment.title = file.name;
      attachment.type = "file";
      attachment.extension = "." + file.type.split("/")[1];
      attachment.user = JSON.parse(localStorage.getItem("user"));
      attachment.content = "File Upload by Select or Drop";
      attachment.thumb = file.dataURL;
      attachment.thumb_list = file.dataURL;
      attachment.isLoading = true;
      attachment.progress = null;
      attachment.size = file.size;
      this.tempAttachments = [...this.tempAttachments, attachment];
    },
    // a middle layer function where you can change the XHR request properties
    sendingFiles(files, xhr, formData) {
      console.log(
        "if you want to change the upload time or add data to the formData you can do it here."
      );

      formData.append("password", this.passwort);
      console.log("Files sending", files);
    },
    // function where we get the upload progress
    uploadProgress(file, progress, bytesSent) {
      console.log("File Upload Progress", progress);
      this.progress = Math.floor(progress);
      this.tempAttachments.map(attachment => {
        if (attachment.title === file.name) {
          attachment.progress = `${Math.floor(progress)}`;
        }
      });
    },
    // called on successful upload of a file
    success(file, response) {
      console.log("File uploaded successfully");
      console.log("Response is ->", response);
    }
  },
  computed: {
    getTempAttachments() {
      return this.tempAttachments;
    },
    getAttachments() {
      return this.attachments;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.dropzone-container {
  border: 1px dashed #ccc;
  border-radius: 15px;
}
</style>
